import { useRef, useEffect } from "react";
import PopupWithForm from "./PopupWithForm";
function EditAvatarPopup({ isOpen, onClose, onUpdateAvatar, isLoading }) {
  const inputValue = useRef("");

  function handleSubmit(e) {
    e.preventDefault();
    onUpdateAvatar({
      avatar: inputValue.current.value,
    });
  }

  useEffect(() => {
    inputValue.current.value = "";
  }, [isOpen]);

  return (
    <PopupWithForm
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      name="avatar"
      title="–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä"
      isLoading={isLoading}
      submitButtonValue="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
      submitButtonWaitingValue="–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ..."
    >
      <fieldset className="popup__input">
        <input
          type="url"
          name="avatarUrl"
          required
          placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∞–≤–∞—Ç–∞—Ä"
          className="popup__input-item popup__input-item_change_image"
          id="sign-in-url"
          autoComplete="off"
          ref={inputValue}
        />
        <span className="popup__input-error" id="sign-in-url-error"></span>
      </fieldset>
    </PopupWithForm>
  );
}

export default EditAvatarPopup;
